/* File: src/scss/pages/mobile_picker.scss */

// --- SCSS Variables (Copied from your admin styles) ---
$primary-color: #0d6efd;
$secondary-color: #6c757d;
$success-color: #198754;
$warning-color: #ffc107;
$danger-color: #dc3545;
$info-color: #0dcaf0;

$app-background-color: #f8f9fa;
$border-color: #dee2e6; // Used for inputs
$text-color-primary: #212529;
$text-color-secondary: #495057;

$card-background-color: #DDEBF3;
$card-border-color: darken($card-background-color, 8%);

$base-font-size: 16px;
$border-radius-small: 8px;
$base-margin: 1rem; // 16px
$card-padding: 1.5rem; // 24px
$base-shadow: 0 4px 12px rgba(0, 0, 0, 0.07);

$font-family-sans-serif: 'Poppins', sans-serif;

// --- Base Styles ---
*, *::before, *::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html {
    font-size: $base-font-size;
}

.mobile-picking-interface {
    font-family: $font-family-sans-serif;
    background-color: $app-background-color;
    color: $text-color-primary;
    line-height: 1.6;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    // Allow content to scroll if needed, center card horizontally
    padding: $base-margin;
    justify-content: flex-start; // Align card to top
    padding-top: 2rem; // Add some space at the top
}

// --- Utility ---
.hidden {
    display: none !important;
}

// --- Picking Card Styles ---
.picking-card {
    background-color: $card-background-color;
    border-radius: $border-radius-small;
    padding: $card-padding;
    box-shadow: $base-shadow;
    border: 1px solid $card-border-color;
    width: 100%;
    max-width: 450px; // Max width for the card
    margin-bottom: 2rem; // Space at the bottom

    &__title {
        font-size: 1.5rem;
        font-weight: 700;
        color: darken($text-color-secondary, 15%);
        text-align: center;
        margin-bottom: 1.5rem;
    }
}

// --- Order Scanning Section ---
.order-scanning {
    margin-bottom: 1.5rem;
    text-align: center; // Center button initially
}

.scanner-container {
    margin-top: 1rem;
    margin-bottom: 1rem;
    border: 1px dashed $secondary-color;
    border-radius: $border-radius-small;
    overflow: hidden; // Contain the reader element

    // Style the reader div provided by the library
    #reader {
        width: 100%;
        // Let the library determine height, or set a fixed aspect ratio
        // aspect-ratio: 1 / 1; // Example: Square scanner
    }
    // Style the library's UI elements if needed (inspect elements to find classes)
    #reader__dashboard_section_swaplink { display: none !important; } // Hide file scan option
    #reader__dashboard_section_csr > span > button { // Style the stop button
        background-color: $danger-color !important;
        color: white !important;
        border: none !important;
        padding: 0.5rem 1rem !important;
        border-radius: $border-radius-small !important;
        margin-top: 0.5rem !important;
        cursor: pointer !important;
    }
}

.scanned-info {
    margin-top: 0.5rem;
    font-size: 0.9rem;
    color: $text-color-secondary;
    min-height: 1.2em; // Reserve space
    font-style: italic;
}


// --- Input & Label Styles ---
.input-label {
    display: block;
    font-size: 0.875rem;
    font-weight: 500;
    color: $text-color-secondary;
    margin-bottom: 0.25rem;
}

.input-group { // Used previously, keep if needed elsewhere
    display: flex;
    gap: 0.5rem;
    margin-bottom: 1rem;
}

.picking-input {
    flex-grow: 1;
    border-radius: $border-radius-small;
    border: 1px solid $border-color;
    padding: 0.65rem 0.75rem;
    font-family: $font-family-sans-serif;
    font-size: 1rem;
    color: $text-color-primary;
    width: 100%;

    &:focus {
       border-color: $primary-color;
       box-shadow: 0 0 0 2px rgba($primary-color, 0.25);
       outline: none;
    }
    &[type=number] {
        -moz-appearance: textfield;
        &::-webkit-outer-spin-button,
        &::-webkit-inner-spin-button {
            -webkit-appearance: none;
            margin: 0;
        }
    }
}

// --- Button Styles ---
.picking-button {
    padding: 0.75rem 1.5rem;
    font-size: 1rem;
    font-weight: 600;
    border-radius: $border-radius-small;
    border: none;
    cursor: pointer;
    transition: background-color 0.2s ease, box-shadow 0.2s ease;
    font-family: $font-family-sans-serif;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    white-space: nowrap;
    gap: 0.5rem; // Space between icon and text

    .material-icons-outlined { // Icon inside button
        font-size: 1.3em;
        vertical-align: middle;
    }

    &--primary {
        background-color: $primary-color;
        color: white;
        &:hover { background-color: darken($primary-color, 10%); }
        &:focus {
            outline: none;
            box-shadow: 0 0 0 3px rgba($primary-color, 0.4);
        }
    }

    &--success {
        background-color: $success-color;
        color: white;
        width: 100%;
         &:hover { background-color: darken($success-color, 10%); }
         &:focus {
            outline: none;
            box-shadow: 0 0 0 3px rgba($success-color, 0.4);
        }
    }
     // Specific style for scan button if needed (e.g., width)
    &--scan {
        width: 100%; // Make scan button full width initially
        margin-bottom: 0.5rem; // Space below scan button
    }
}

// --- Task Display Styles ---
.task-display {
    margin-top: 1.5rem; // Add space above task display
    margin-bottom: 1.5rem;
    padding-top: 1rem;
    border-top: 1px solid $card-border-color;

    & > * { margin-bottom: 0.75rem; }
    & > *:last-child { margin-bottom: 0; }

    &__subtitle {
        font-size: 1.125rem;
        font-weight: 600;
        color: $text-color-primary;
    }
    &__product-info {
        font-size: 1.125rem;
        color: $text-color-primary;
        strong { font-weight: 600; }
    }
    &__location {
        font-weight: 500;
        color: $primary-color;
    }
    &__detail-value {
         font-weight: 500;
         color: $text-color-primary;
    }
    &__quantity-needed {
        font-size: 1.75rem;
        font-weight: 700;
        text-align: center;
        color: $success-color;
        margin-top: 0.5rem;
    }
    &__availability-info {
        font-size: 0.875rem;
        text-align: center;
    }
}

.icon-text {
    display: flex;
    align-items: center;
    gap: 0.4rem;
    color: $text-color-secondary;
    font-size: 1rem;
    .material-icons-outlined {
        font-size: 1.3em;
        color: $secondary-color;
        flex-shrink: 0;
    }
    strong {
        color: $text-color-primary;
        font-weight: 500;
    }
}

.details-text {
    color: $text-color-secondary;
    font-size: 0.875rem;
}

// --- Confirmation Area Styles ---
.confirmation-area {
    margin-bottom: 1.5rem;
    .picking-input { margin-bottom: 1rem; }
}

// --- Message Area Styles ---
.message-area {
    min-height: 1.5em;
    text-align: center;
    font-size: 0.875rem;
    font-weight: 500;
    margin-top: 1rem;
    &--error { color: $danger-color; }
    &--success { color: $success-color; }
}

// --- All Done Message Styles ---
.all-done-message {
    margin-top: 1.5rem;
    text-align: center;
    font-size: 1.25rem;
    font-weight: 600;
    color: $primary-color;
    .material-icons-outlined {
        font-size: 2.5rem;
        display: block;
        margin-bottom: 0.5rem;
    }
}

// --- Loading Overlay & Spinner ---
#loading-overlay {
    position: fixed;
    inset: 0;
    background-color: rgba($app-background-color, 0.85);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 50;
}

.spinner {
    border: 4px solid rgba($text-color-primary, 0.1);
    width: 36px;
    height: 36px;
    border-radius: 50%;
    border-left-color: $primary-color;
    animation: spin 1s ease infinite;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}
